.timeline-wrapper {
    padding-top: 80px;
    padding-bottom: 80px;
    position: relative;
    .btn-wrapper {
      width: 100%;
      text-align: center;
      margin-top: 50px;
      @media(max-width: 800px) {
        text-align: left;
        margin-left: 120px;
        margin-top: -60px;
      }
    }
    .line {
      height: 1px;
      width: 1200%;
      top: 0;
      left: 0;
      bottom: 0;
      margin: auto;
      position: absolute;
      border-top: 4px solid  $color-primary-2;
      // z-index: -1;
      @media(max-width: 800px) {
        // display: none;
        width: 100%;
        height: 100%;
        border: 0;
        border-left: 4px solid $color-primary-2;
        left: 30px;
      }
    }
  }
  
  .timeline {
    position: relative;
    width: 100vw;
    padding: 20px 0;
    z-index: 5;
    @media(max-width: 800px) {
      width: auto;
      margin: 7px;
      margin-bottom: -75px;
    }
    .node-wrapper {
      padding-right: 100px;
      padding-left: 100px;
      display: flex;
      // justify-content: space-between;
      @media(max-width: 800px) {
        display: block;
        padding: 0;
      }
    }
  }
  
  .tl-node {
    position: relative;
    margin-right: 100px;
    margin-left: 100px;
    @media(max-width: 800px) {
      margin: auto;
      margin-bottom: 75px;
      display: flex;
      &:not(:last-child){
        margin-bottom: 75px;
      }
      &:nth-child(-n+3) {
      }
    }
    p {
  
      color: $color-primary-2;;
      font-weight: 900;
      margin: 0;
      font-size: 2rem;
      text-align: center;
      position: absolute;
      top: -35px;
      left: 0px;
      width: 100%;
      font-family: inherit;
      letter-spacing: .8px;
      @media(max-width: 800px) {
        display: none;
      }
    }
    h3, h5 {
      text-align: center;
      font-family: inherit;
      margin: 0;
    }
    h3{
      color: $color-text;
      text-align: center;
      font-size: 15px;
      font-weight: normal;
      line-height: 1.5;
      margin-bottom: 5px;
    }
    h5 {
      color: $color-header;
      font-size: 20px;
      margin-bottom: 5px;
      font-weight: 900;
      letter-spacing: .8px;
    }
    .data {
      opacity: 0;
      transition: all 400ms;
      position: absolute;
      width: 250px;
      z-index: 1;
      background-color: $color-white;
      visibility: hidden;
      top: 75px;
      left: -20px;
      border: 2px solid $color-primary-2;
      box-shadow: 0 5px 9px -5px $color-primary-2;
      img {
        width: 100%;
        height: auto;
        display: block;
      }
      .upper {
        padding: 12px;
      }
  
      @media(max-width: 800px) {
        position: relative;
        top: -8px;
        left: auto;
        visibility: visible;
        opacity: 1;
        margin-left: 35px;
        width: 80%;
      }
    }
    .dot {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-image: url("../img/main-icon.png");
      background-size: 100% 100%;
      // transition: all .3s;
  
      // background-color: #808080;
      transition: all 200ms;
      position: relative;
      // border: 4px solid #808080;
      @media(max-width: 800px) {
        // background-color: #000;
        // border: 4px solid #fff;
      }
    }
    &:hover {
      .dot {
        transform: scale(1.2);
      }
      .data {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  
  .left-triangle {
    &:before {
      content: '';
      width: 0;
      height: 0;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-bottom: 15px solid $color-primary-2;
      position: absolute;
      top: -16px;
      left: 17px;
      @media(max-width: 800px) {
        top: 15px;
        left: -33px !important;
        border-top: 17px solid transparent;
        border-bottom: 17px solid transparent;
        border-right: 17px solid $color-primary-2;
      }
    }
  }
  .right-triangle {
    &:before {
      content: '';
      width: 0;
      height: 0;
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      border-bottom: 15px solid $color-primary-2;
      position: absolute;
      top: -16px;
      right: 20px;
      @media(max-width: 800px) {
        top: 15px;
        left: -40px !important;
        border-top: 20px solid transparent;
        border-bottom: 20px solid transparent;
        border-right:20px solid $color-white;
      }
    }
  }
  
  
  .timeline-full {
    @media(max-width: 800px) {
      width: auto;
      margin: 0;
    }

    .line {
      width: 100%;
      height: 100%;
      border: 0;
      border-left: 2px solid $color-third-5;
      left: 15px;
    }

    .node-wrapper {
      display: block;
      position: relative;
      max-width: 900px;
      margin: auto;
      padding: 0;
    }

    .tl-node {
      display: flex;
      margin: auto;
      margin-bottom: 50px;
    }

    p {
      display: none;
    }

    .data {
      position: relative;
      top: -20px;
      left: auto;
      visibility: visible;
      opacity: 1;
      margin-left: 50px;
      width: 80%;
      img {
        width: 100%;
        height: auto;
      }
    }

    .dot {
      background-color: $color-black;
      border: 4px solid $color-white;
    }
  
    .left-triangle:before, .right-triangle:before {
      top: 15px;
      left: -40px !important;
      border-top: 20px solid transparent;
      border-bottom: 20px solid transparent;
      border-right:20px solid $color-white;
    }
  }
  